import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
let inviteAdmin = class inviteAdmin extends Vue {
    constructor() {
        super(...arguments);
        this.schoolId = '';
        this.timestamp = '';
    }
    mounted() {
        let state = this.$route.query.state;
        let code = this.$route.query.code;
        // wx.closeWindow();
        this.schoolId = state;
    }
};
inviteAdmin = __decorate([
    Component({
        name: 'inviteAdmin'
    })
], inviteAdmin);
export default inviteAdmin;
